<script>
// import SelectElement from './SelectElement.vue';
import { mapGetters, mapActions, mapMutations } from 'vuex';
import { ElMessage } from 'element-plus'

export default {
    props: [
        'mode_change', 'index_area', 'connection_id', 'driver'
    ],
    components: {
        // SelectElement,
    },
    emits: ['closeEnd', 'onBigType'],
    watch: {
        'areaParam.name'(newVal) {
            let flag = false;
            let list_mistake = 'Строка пуста';
            const re = /[А-яЁё]/;
            if (newVal) {
                if (newVal.indexOf(' ') == -1) {
                    if (newVal.search(re) == -1) {
                        if (newVal[0].search(/[0-9]/) == -1) {
                            flag = true;
                        } else {
                            list_mistake = 'Название должно начинаться только с символа латинского алфавита';
                        }
                    } else {
                        list_mistake = 'Название должно содержать только символы латинского алфавита';
                    }
                } else {
                    list_mistake = 'Название должно быть без пробелов';
                }
            }
            if (flag) {
                this.correctName.status = true;
                this.correctName.text = '';
            } else {
                this.correctName.status = false;
                this.correctName.text = list_mistake;
            }
            this.checkStatusForm();
        },
        'areaParam.number_db'(newVal) {
            let flag = false;
            let list_mistake = 'Строка пуста';
            if (newVal || newVal==0) {
                if (newVal>=0) {
                    flag = true;
                } else {
                    list_mistake = 'Поле должно содержать число большее либо равное 0'
                }
            }
            if (flag) {
                this.correctNumberDB.status = true;
                this.correctNumberDB.text = '';
            } else {
                this.correctNumberDB.status = false;
                this.correctNumberDB.text = list_mistake;
            }
            this.checkStatusForm();
        },
        'areaParam.start_byte'(newVal) {
            let flag = false;
            let list_mistake = 'Строка пуста';
            if (newVal || newVal==0) {
                if (newVal>=0) {
                    flag = true;
                } else {
                    list_mistake = 'Поле должно содержать число большее либо равное 0'
                }
            }
            if (flag) {
                this.correctStartByte.status = true;
                this.correctStartByte.text = '';
            } else {
                this.correctStartByte.status = false;
                this.correctStartByte.text = list_mistake;
            }
            this.checkStatusForm();
        },
        'areaParam.offset'(newVal) {
            let flag = false;
            let list_mistake = 'Строка пуста';
            if (newVal || newVal==0) {
                if (newVal>0) {
                    flag = true;
                } else {
                    list_mistake = 'Поле должно содержать число больше 0'
                }
            }
            if (flag) {
                this.correctOffset.status = true;
                this.correctOffset.text = '';
            } else {
                this.correctOffset.status = false;
                this.correctOffset.text = list_mistake;
            }
            this.checkStatusForm();
        },
        'areaParam.slave_id'(newVal) {
            let flag = false;
            let list_mistake = 'Строка пуста';
            if (newVal || newVal==0) {
                if (newVal>0) {
                    flag = true;
                } else {
                    list_mistake = 'Поле должно содержать число больше 0'
                }
            }
            if (flag) {
                this.correctSlaveId.status = true;
                this.correctSlaveId.text = '';
            } else {
                this.correctSlaveId.status = false;
                this.correctSlaveId.text = list_mistake;
            }
            this.checkStatusForm();
        },
        'areaParam.start_register'(newVal) {
            let flag = false;
            let list_mistake = 'Строка пуста';
            if (newVal || newVal==0) {
                if (newVal>=0) {
                    flag = true;
                } else {
                    list_mistake = 'Поле должно содержать число большее либо равное 0'
                }
            }
            if (flag) {
                this.correctStartRegister.status = true;
                this.correctStartRegister.text = '';
            } else {
                this.correctStartRegister.status = false;
                this.correctStartRegister.text = list_mistake;
            }
            this.checkStatusForm();
        },
        'areaParam.size'(newVal) {
            let flag = false;
            let list_mistake = 'Строка пуста';
            if (newVal || newVal==0) {
                if (newVal>=0) {
                    flag = true;
                } else {
                    list_mistake = 'Поле должно содержать число большее либо равное 0'
                }
            }
            if (flag) {
                this.correctSize.status = true;
                this.correctSize.text = '';
            } else {
                this.correctSize.status = false;
                this.correctSize.text = list_mistake;
            }
            this.checkStatusForm();
        },
        'areaParam.type_area'() {
            this.checkStatusForm();
        },
        'areaParam.function'() {
            this.checkStatusForm();
        }
    },
    data() {
        return {
            allFormCorrect: false,
            correctName: {
                status: false,
                text: 'Строка пуста',
                hoverSign: false,
            },
            correctNumberDB: {
                status: false,
                text: 'Строка пуста',
                hoverSign: false,
            },
            correctStartByte: {
                status: false,
                text: 'Строка пуста',
                hoverSign: false,
            },
            correctOffset: {
                status: false,
                text: 'Строка пуста',
                hoverSign: false,
            },
            correctSlaveId: {
                status: false,
                text: 'Строка пуста',
                hoverSign: false,
            },
            correctSize: {
                status: false,
                text: 'Строка пуста',
                hoverSign: false,
            },
            correctStartRegister: {
                status: false,
                text: 'Строка пуста',
                hoverSign: false,
            },
            areaParam: {
                name: '',
                number_db: null,
                start_byte: null,
                offset: null,
                connection: ''
            },
            drivers: [
                {
                    key: 1,
                    label: 'DB',
                    value: 'DB'
                },
                {
                    key: 2,
                    label: 'PA',
                    value: 'PA'
                }
            ],
            function_kind: [
                {
                    key: 1,
                    label: '3',
                    value: 3
                },
                {
                    key: 2,
                    label: '4',
                    value: 4
                }
            ]
        }
    },
    methods: {
        checkStatusForm() {
            if ((this.driver == 'ModbusTCP' && this.correctName.status &&
                    this.correctSlaveId.status && this.correctSize.status && this.correctStartRegister.status) || 
                (this.driver == 'Snap7' && this.correctName.status &&
                    (this.correctNumberDB.status || this.areaParam.type_area == 'PA') && this.correctStartByte.status && this.correctOffset.status)) {
                this.allFormCorrect = true;
            } else {
                this.allFormCorrect = false;
            }
        },
        async saveMethod() {
            // let validStatus = this.checkParam() #TODO
            if (this.allFormCorrect) {
                if (!this.mode_change) {
                    let item = {
                        param: this.areaParam,
                        id: null,
                        connection_id: this.connection_id
                    }
                    await this.checkAreaName(item);
                    if (this.checkName) {
                        this.areaParam.connection = 'http://127.0.0.1:8000/connection/'+this.connection_id+'/'
                        await this.addNewArea(this.areaParam)
                        this.areaParam.connection = ''
                        this.$emit('closeEnd');
                    } else {
                        ElMessage({
                            dangerouslyUseHTMLString: true,
                            type: 'error',
                            message: 'This name already exists',
                        })
                    }
                } else {
                    let item = {
                        param: this.areaParam,
                        id: this.areaList[this.index_area].id,
                        connection_id: this.connection_id
                    }
                    await this.checkAreaName(item);
                    if (this.checkName) {
                        this.areaParam.connection = 'http://127.0.0.1:8000/connection/'+this.connection_id+'/'
                        this.changeArea({
                            id_area: this.areaList[this.index_area].id,
                            data: this.areaParam
                        })
                        this.areaParam.connection = ''
                        this.$emit('closeEnd');
                    } else {
                        ElMessage({
                            dangerouslyUseHTMLString: true,
                            type: 'error',
                            message: 'This name already exists',
                        })
                    }
                }
            }
            
        },
        ...mapActions({
            deleteArea: 'deleteArea',
            addNewArea: 'addNewArea',
            changeArea: 'changeArea',
            checkAreaName: 'checkAreaName',
        }),
        ...mapMutations({
            setStatusName: 'setNameAreaStaus'
        }),
        checkParam() {
            let answer = false
            if ('string' == typeof this.areaParam.name && this.areaParam.name.split(' ').length == 1) {
                
                if (this.areaParam.type_area == 'DB' && this.areaParam.number_db) {
                    if (this.areaParam.start_byte && this.areaParam.offset) {
                        answer = true
                    }
                } else if (this.areaParam.type_area == 'PA') {
                    if (this.areaParam.start_byte && this.areaParam.offset) {
                        answer = true
                    }
                }
            }
            return answer
        }
    },
    computed: {
        ...mapGetters({
            areaList: 'getAreaList',
            checkName: 'getAreaStatusName'
        })
    },
    mounted() {
        this.setStatusName(false);
        if (this.mode_change) {
            this.areaParam.name = this.areaList[this.index_area].name
            this.areaParam.type_area = this.areaList[this.index_area].type_area
            this.areaParam.number_db = this.areaList[this.index_area].number_db
            this.areaParam.start_byte = this.areaList[this.index_area].start_byte
            this.areaParam.offset = this.areaList[this.index_area].offset
            this.areaParam.slave_id = this.areaList[this.index_area].slave_id
            this.areaParam.function = this.areaList[this.index_area].function
            this.areaParam.size = this.areaList[this.index_area].size
            this.areaParam.start_register = this.areaList[this.index_area].start_register
        }
    },
}
</script>

