<template>
    <div class="window">
        <div class="header">
            <h2>Areas of {{ ConnectionList[check].name }}</h2>
            <div class="button-close" @click="closeWindow">×</div>
        </div>
        <Transition name="fade" mode="out-in">
        <div class="header-form-plase" v-if="!loaderAreaList">
            <ButtonNewArea
                :connection_id="ConnectionList[check].id"
                :indexConnection="check"
                :driver="ConnectionList[check].driver"
            />
            <div>
                <div class="vl" />
            </div>
            <TransitionGroup name="list">
            <div v-for="item, index in areaList" :key="item">
                <AreaLine
                    :area_param="item"
                    :area_index="index"
                    :indexConnection="check"
                />
            </div>
            </TransitionGroup>
        </div>
        <div class="load-plase" v-else>
            <LoaderIcon />
        </div>
        </Transition>
    </div>
</template>

<script>
import { mapMutations, mapGetters, mapActions } from 'vuex';
import LoaderIcon from './LoaderIcon.vue';
import ButtonNewArea from './simpleElement/ButtonNewArea.vue';
// import FormArea from './simpleElement/FormArea.vue'
import AreaLine from './simpleElement/AreaLine.vue'

export default {
    data() {
        return {
            showForm: false
        }
    },
    components: {
        LoaderIcon,
        // FormArea,
        ButtonNewArea,
        AreaLine
    },
    computed: {
        ...mapGetters({
            ConnectionList: 'getConnectionList',
            check: 'getCheckConnectionBar',
            areaList: 'getAreaList',
            loaderAreaList: 'getLoadAreaWindow',
        })
    },
    methods: {
        ...mapMutations({
            setWindowAreas: 'setWindowAreas',
            setStatusWindowVariableParamStatus: 'setStatusWindowVariableParamStatus',
            setStatusWindowVariableParam: 'setStatusWindowVariableParam'
        }),
        ...mapActions({
            deleteArea: 'deleteArea'
        }),
        closeWindow() {
            this.setStatusWindowVariableParamStatus(false)
            this.setStatusWindowVariableParam({
                var: {},
                area_id: null,
                driver: null
            })
            this.setWindowAreas(false)
        }
    }
}
</script>

