<template>
<div class="variable-list-place">
    <TransitionGroup name="list">
        <div class="variable-element"
            @click="pickVariable(item)"
            :class="{
                active: item.id==Variable.vareable.id,
                diactive: item.id!=Variable.vareable.id
            }"
            v-for="item in variableList" :key="item.id"
        >
            <p>{{ item.name }}</p>
        </div>
    </TransitionGroup>
</div>
</template>

<script>
import { mapMutations, mapGetters } from 'vuex'

export default {
    data() {
        return {
        }
    },
    props:['variableList', 'area_id', 'driver'],
    computed: {
        ...mapGetters({
            Variable: 'getWindowVariableParam',
        }),
    },
    methods: {
        ...mapMutations({
            setStatusWindowVariableParamStatus :'setStatusWindowVariableParamStatus',
            setStatusWindowVariableParam: 'setStatusWindowVariableParam'
        }),
        pickVariable(item) {
            if (this.Variable.vareable.id == item.id) {
                this.setStatusWindowVariableParamStatus(false)
                this.setStatusWindowVariableParam({
                    var: {},
                    area_id: null,
                    driver: null
                })
            } else {
                this.setStatusWindowVariableParamStatus(true)
                this.setStatusWindowVariableParam({
                    var: item,
                    area_id: this.area_id,
                    driver: this.driver
                })
            }
        }
    }
}
</script>

<style scoped lang="scss">
.variable-list-place {
    margin-left: 0.26vw;
}
.variable-element {
    
    height: 30px;
    cursor: pointer;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 5px;
    transition: width .6s, background .6s, color .6s;
}
.active {
    width: 499px;
    background: #272A43;
    color: #F1F3FF;
}
.diactive {
    width: 250px;
    background: #7E92A9;
}
.list-move, .list-enter-active, .list-leave-active {
    transition: all 0.5s ease, height 0.5s;
}

.list-enter-from, .list-leave-to {
    opacity: 0;
    transform: translateX(30px);
    /* transform: translateY(-100%); */
}

.list-leave-active {
    position: absolute;
}
</style>