<template>
    <div class="main-bar">
        <div class="button-add" @click="OpenWindowCreateConnect(true)">
            <div class="icon-plus">+</div>
            <label>New connection</label>
        </div>
        <div class="vl" />
        <div class="place-list-connection">
            <TransitionGroup name="list">
                <div v-for="item, index in ConnectionList" :key="item" class="one-line-connection"
                @click="pickConnection(index)"  @click.right="handler($event, index)"
                >
                    <div class="check-flag"
                    :class="{'on-flag': check == index, 'off-flag': !(check == index) }" />
                    <div class="el-connection"
                    :class="{'status-connect': item.switcher && item.status_connection,
                             'status-disconnect': item.switcher && !item.status_connection,
                             'on-el-connection': item.status_process,
                             'off-el-connection': !item.status_process}">
                        <div class="header-el-connection">
                            <div class="switcher-area">
                                <input class="switcher" type="checkbox" @click.stop="changeSwitcher(item.id)"
                                    v-model="item.switcher"
                                >
                            </div>
                        </div>
                        <div class="body-el-connection">
                            <img src="@/assets/img/Snap-logo.svg" alt="Alt">
                            <label>{{item.driver}}</label>
                        </div>
                        <div class="bottom-el-connection">{{item.name}}</div>
                        <div class="addition-button" :class="{
                            'up-position': chooseRightClick == index,
                            'down-position': chooseRightClick != index,
                        }">
                            <button @click.stop.prevent="openChangeWindow(item)">
                                <img src="@/assets/img/change-connect.svg" alt="">
                            </button>
                            <button @click.stop.prevent="deleteConnection(item.id)">
                                <img src="@/assets/img/remove-connect.svg" alt="">
                            </button>
                        </div>
                    </div>
                </div>
            </TransitionGroup>
        </div>
    </div>
</template>
  
<script>
import { mapMutations, mapActions, mapGetters } from 'vuex';
import axios from '../../config-axios'

export default {
    name: 'ConnectionBar',
    data() {
        return {
            status: {},
            test: false,
            chooseRightClick: null
        }
    },
    computed: {
        ...mapGetters({
            ConnectionList: 'getConnectionList',
            check: 'getCheckConnectionBar'
        })
    },
    methods: {
        deleteConnection(id) {
            this.chooseRightClick = null
            this.deleteConnection(id)
        },
        openChangeWindow(item) {
            this.OpenWindowCreateConnect(true)
            this.pushConParamToWindow(item)
        },
        handler: function(e, index) {
            if (index == this.chooseRightClick) {
                this.chooseRightClick = null
            } else {
                this.chooseRightClick = index
            }
            e.preventDefault();
        },
        ...mapMutations({
            OpenWindowCreateConnect: 'setWindowCreateConnection',
            changeMainBackground: 'setMainBackground',
            OpenWindowAreas: 'setWindowAreas',
            pickConnectionClick: 'setCheckConnectionBar',
            pushConParamToWindow: 'setConParamToWindow'
        }),
        ... mapActions({
            getAllConnections: 'getAllConnections',
            getAreaFromConnection: 'getAreaFromConnection',
            StatusConnections: 'getStatusConnections',
            deleteConnection: 'deleteConnection'
        }),
        pickConnection(index) {
            if (this.check!=index) {
                this.getAreaFromConnection(this.ConnectionList[index].id)
            }
            
            this.pickConnectionClick(index)
        },
        changeSwitcher(id) {
            axios.post('connection/status/', {id:id})
        },
        startTimer() {
            this.StatusConnections()
            setTimeout(this.startTimer, 3000);
        },
    },
    created() {
        this.getAllConnections()
    },
    mounted() {
        this.StatusConnections()
        setTimeout(this.startTimer, 100);

    }
}
</script>

