import { createStore } from 'vuex'
import axios from '../config-axios'
import { ElMessage } from 'element-plus'

export default createStore({
    state: {
        loginStatus: true,
        processRequest: false,
        backgroundStraight: false,
        connectionBar: {
            ConnectionList: [],
            check: null
        },
        areaList: [],
        windows: {
            CreateConnection: {
                status: false,
                loadRequest: false,
                param: null,
                nameStatus: false
            },
            Areas: {
                status: false,
                loadRequest: false,
                nameStatus: false
            },
            VareableAdd: {
                status: false,
                headName: '',
                area: null,
                nameStatus: false
            },
            VareableParam: {
                status: false,
                vareable: {},
                area_id: null
            }
        }
    },
    getters: {
        getLoginStatus(state) {
            return state.loginStatus
        },
        getProcessRequest(state) {
            return state.processRequest
        },
        getloadConnection(state) {
            return state.windows.CreateConnection.loadRequest
        },
        getLoadAreaWindow(state) {
            return state.windows.Areas.loadRequest
        },
        getBackgroundMainWindow(state) {
            return state.backgroundStraight
        },
        getStatusWindowCreateConnectiob(state) {
            return state.windows.CreateConnection.status
        },
        getParamWindowCreateConnectiob(state) {
            return state.windows.CreateConnection.param
        },
        getStatusWindowAreas(state) {
            return state.windows.Areas.status
        },
        getStatusWindowVariableAdd(state) {
            return state.windows.VareableAdd.status
        },
        getStatusWindowVariableParam(state) {
            return state.windows.VareableParam.status
        },
        getWindowVariableParam(state) {
            return state.windows.VareableParam
        },
        getParamWindowVariableAdd(state) {
            return state.windows.VareableAdd
        },
        getConnectionList(state) {
            return state.connectionBar.ConnectionList
        },
        getCheckConnectionBar(state) {
            return state.connectionBar.check
        },
        getAreaList(state) {
            return state.areaList
        },
        getConnectionStatusName(state) {
            return state.windows.CreateConnection.nameStatus
        },
        getAreaStatusName(state) {
            return state.windows.Areas.nameStatus
        },
        getVariableStatusName(state) {
            return state.windows.VareableAdd.nameStatus
        }
    },
    mutations: {
        setLoginStatus(state, data) {
            state.loginStatus = data
        },
        setStatusProcessRequest(state, data) {
            state.processRequest = data
        },
        setWindowVariableAdd(state, data) {
            if (data == true) {
                state.windows.VareableParam.status = false
                state.windows.VareableParam.vareable = {}
            }
            state.windows.VareableAdd.status = data
        },
        setDriverForWindowVariableAdd(state, data) {
            state.windows.VareableAdd.driver = data
        },
        setStatusWindowVariableParamStatus(state, data) {
            if (data == true) {
                state.windows.VareableAdd.status = false
            }
            state.windows.VareableParam.status = data
            state.windows.VareableParam.vareable = {}
        },
        setStatusWindowVariableParam(state, data) {
            state.windows.VareableParam.vareable = data.var
            state.windows.VareableParam.area_id = data.area_id
            state.windows.VareableParam.driver = data.driver
        },
        setWindowVariableAddName(state, data) {
            state.windows.VareableAdd.headName = data
        },
        setWindowVariableAddArea(state, data) {
            state.windows.VareableAdd.area = data //state.connectionBar.ConnectionList[data.indexConnection].area[data.indexArea]
        },
        setWindowCreateConnection(state, data) {
            state.windows.CreateConnection.status = data
            state.windows.CreateConnection.param = null
            //close all other windows
            state.windows.Areas.status = false
            state.windows.VareableAdd.status = false
            state.windows.VareableParam.status = false
            state.windows.VareableParam.vareable = {}
            state.connectionBar.check = null
            state.backgroundStraight = false
        },
        setConParamToWindow(state, data) {
            state.windows.CreateConnection.param = data
        },
        setCheckConnectionBar(state, data) {
            if (state.connectionBar.check == data) {
                state.connectionBar.check = null
                state.backgroundStraight = false
                state.windows.Areas.status = false
                state.windows.VareableAdd.status = false
                state.windows.VareableParam.status = false
                state.windows.VareableParam.vareable = {}
            } else {
                state.connectionBar.check = data
                state.backgroundStraight = true
                state.windows.Areas.status = true

                //close all other windows
                state.windows.CreateConnection.status = false
            }
        },
        setConnectionList(state, data) {
            state.connectionBar.ConnectionList = data
        },
        addNewConnection(state, data) {
            state.connectionBar.ConnectionList.push(data)
        },
        changeConnection(state, data) {
            let indexCon = null
            state.connectionBar.ConnectionList.find( (el, index) => {
                if (data.id == el.id) {
                    indexCon = index
                } 
            })
            if (indexCon != null) {
                state.connectionBar.ConnectionList[indexCon] = data
            }
        },
        deleteConnection(state, data) {
            let indexCon = null
            state.connectionBar.ConnectionList.find( (el, index) => {
                if (data == el.id) {
                    indexCon = index
                } 
            })
            if (indexCon != null) {
                state.connectionBar.ConnectionList.splice(indexCon, 1)
            }
        },
        setMainBackground(state, data) {
            state.backgroundStraight = data
        },
        setloadConnection(state, data) {
            state.windows.CreateConnection.loadRequest = data
        },
        setLoaderAreaWindow(state, data) {
            state.windows.Areas.loadRequest = data
        },
        setWindowAreas(state, data) {
            if (data == false) {
                state.connectionBar.check = null,
                state.backgroundStraight = false
                state.windows.Areas.status = false
                state.windows.VareableAdd.status = false
                state.windows.VareableParam.status = false
            } else {
                state.windows.Areas.status = data

                //close all other windows
                state.windows.CreateConnection.status = false
            }
        },
        setNewAreaList(state, data) {
            state.areaList = data
        },
        setNewArea(state, data) {
            state.areaList.push(data)
        },
        setRemoveArea(state, data) {
            state.areaList.find((el, index) => {
                if (el.id == data) {
                    state.areaList.splice(index, 1)
                }
            })
        },
        changeAreaInConnection(state, data) {
            let indexArea = null
            state.areaList.find((elArea, inAr) => {
                if (elArea.id == data.id) {
                    indexArea =inAr
                }
            })
            if (indexArea!=null) {
                state.areaList[indexArea] = data
            }
        },
        setNewVariable(state, data) {
            state.areaList.find( el => {
                if (el.id == data.area_id) {
                    el.value.push(data)
                }
            })
        },
        setChangedVariable(state, data) {
            let indexArea = null
            let indexValue = null
            state.areaList.find((elArea, inAr) => {
                if (elArea.id == data.area_id) {
                    indexArea = inAr
                    elArea.value.find((elVal, inVal) => {
                        if (elVal.id == data.id) {
                            indexValue = inVal
                        }
                    })
                }
            })
            if (indexArea != null && indexValue != null) {
                state.areaList[indexArea].value[indexValue] = data
                state.windows.VareableParam.vareable = data
            }
        },
        setRemoveVariable(state, data) {
            let indexArea = null
            let indexValue = null
            state.areaList.find((elAr, inAr) => {
                if (elAr.id == data.area_id) {
                    indexArea = inAr
                    elAr.value.find((elVal, inVal) => {
                        if (elVal.id == data.id) {
                            indexValue = inVal
                        }
                    })
                }
            })
            if (indexArea != null && indexValue != null) {
                state.areaList[indexArea].value.splice(indexValue, 1)
            }
        },
        setStatusConnections(state, data) {
            let keys = Object.keys(data)
            keys.forEach(el => {
                state.connectionBar.ConnectionList.find(con => {
                    if (con.id == Number(el)) {
                        con.status_connection = data[el].status_connection
                        con.switcher = data[el].switcher
                        con.status_process = data[el].status_process
                    }
                })
            })
        },

        // Mutations for check correct name for unique name of section
        setNameConnectionStaus(state, data) {
            state.windows.CreateConnection.nameStatus = data
        },
        setNameAreaStaus(state, data) {
            state.windows.Areas.nameStatus = data
        },
        setNameVariableStaus(state, data) {
            state.windows.VareableAdd.nameStatus = data
        }
        // end mutation of check name
    },
    actions: {
        async postLogin(context, data){
            context.commit('setStatusProcessRequest', true)
            axios.defaults.headers.common = {}
            await axios.post('user/login/', data).then( response => {
                context.commit('setLoginStatus', true)
                localStorage.setItem('token', response.data.token)
                axios.defaults.headers.common = {'Authorization': `Token ${response.data.token}`}
            }).catch( error => {
                console.log(error)
                ElMessage({
                    dangerouslyUseHTMLString: true,
                    type: 'error',
                    message: 'Неверный логин или пароль',
                })
                context.commit('setLoginStatus', false)
            })
            context.commit('setStatusProcessRequest', false)
        },
        async checkUserInfo(context) {
            await axios.get('user/info/').then( () => {
                context.commit('setLoginStatus', true)
            }).catch( error => {
                console.log(error)
                context.commit('setLoginStatus', false)
            })
            // console.log(context)
        },
        async addConnection(context, data) {
            context.commit('setloadConnection', true)
            await axios.post('connection/', data).then(response => {
                context.commit('addNewConnection', response.data)
                context.commit('setWindowCreateConnection', false)
            }).catch(error => {
                console.log(error)
            })
            context.commit('setloadConnection', false)
        },
        async changeConnection(context, data) {
            context.commit('setloadConnection', true)
            await axios.put('connection/'+data.id+'/', data.param).then(response => {
                context.commit('changeConnection', response.data)
                context.commit('setWindowCreateConnection', false)
            }).catch(error => {
                console.log(error)
            })
            context.commit('setloadConnection', false)
        },
        async deleteConnection(context, data) {
            await axios.delete('connection/'+data+'/').then(() => {
                context.commit('deleteConnection', data)
            })
        },
        async getAllConnections(context) {
            // context.commit('setStatusProcessRequest', true)
            await axios.get('connection/').then(response => {
                context.commit('setConnectionList', response.data)
            })
            // context.commit('setStatusProcessRequest', false)
        },
        async getAreaFromConnection(context, data) {
            context.commit('setLoaderAreaWindow', true)
            await axios.get('connection/'+data+'/area').then(response => {
                context.commit('setNewAreaList', response.data)
            }).catch(error => {
                ElMessage({
                    dangerouslyUseHTMLString: true,
                    type: 'error',
                    message: 'Ошибка с запросом Area',
                })
                console.log(error)
            })
            context.commit('setLoaderAreaWindow', false)
            
        },
        async addNewArea(context, data) {
            await axios.post('area/', data).then( response => {
                context.commit('setNewArea', response.data)
            }).catch(error => {
                console.log(error)
            })
        },
        async changeArea(context, data) {
            await axios.put('area/'+data.id_area+'/', data.data).then(response => {
                context.commit('changeAreaInConnection', response.data)
            })
        },
        async deleteArea(context, data) {
            await axios.delete('area/'+data).then(() => {
                context.commit('setRemoveArea', data)
            }).catch(error => {
                console.log(error)
            })
        },
        async addNewVareable(context, data) {
            await axios.post('value/', data).then(response => {
                context.commit('setNewVariable', response.data)
                context.commit('setWindowVariableAdd', false)
            })
        },
        async changeVareable(context, data) {
            await axios.put('value/'+data.id+'/', data.data).then(response => {
                context.commit('setChangedVariable', response.data)
            })
        },
        async deleteVareable(context, data) {
            await axios.delete('value/'+data.id).then(() => {
                context.commit('setStatusWindowVariableParamStatus', false)
                context.commit('setRemoveVariable', data)
            })
        },
        async getStatusConnections(context) {
            await axios.get('connection/status/').then(response => {
                context.commit('setStatusConnections', response.data)
            })
        },
        async checkConnectionName(context, data) {
            let param = {
                name: data.param.name,
                id: data.id,
                field: 'connection'
            }
            await axios.post('connection/check_name/', param).then(response => {
                context.commit('setNameConnectionStaus', response.data)
            })
        },
        async checkAreaName(context, data) {
            let param = {
                name: data.param.name,
                id: data.id,
                connection_id: data.connection_id,
                field: 'area'
            }
            await axios.post('connection/check_name/', param).then(response => {
                context.commit('setNameAreaStaus', response.data)
            })
        },
        async checkVariableName(context, data) {
            let param = {
                name: data.param.name,
                id: data.id,
                area_id: data.area_id,
                field: 'variable'
            }
            await axios.post('connection/check_name/', param).then(response => {
                context.commit('setNameVariableStaus', response.data)
            })
        },
    }
})