                <div class="name-line">Name connection:</div>
                <div class="input-form">
                    <div class="form-line-with-mark">
                        <input type="text" v-model="connectionParam.name">
                        <img src="@/assets/img/formGood.svg" v-if="correctName.status" alt="">
                        <img
                            src="@/assets/img/formBad.svg"
