                            @mouseleave="correctIp.hoverSign = false"
                            v-if="!correctIp.status"
                        >
                        <Transition name="fadebuttom">
                            <p
                                class="description-about-bad-form"
                                v-if="!correctIp.status && correctIp.hoverSign"
                            >{{ correctIp.text }}</p>
                        </Transition>
                    </div>
                    <div class="discription-line">
