<script>
import { mapMutations, mapActions, mapGetters } from 'vuex'
import LoaderIcon from './LoaderIcon.vue';
import { ElMessage } from 'element-plus'

export default {
    name: 'WindowCreateConnection',
    components: {
        LoaderIcon
    },
    computed: {
        ...mapGetters({
            procReques: 'getloadConnection',
            ParamConnectiob: 'getParamWindowCreateConnectiob',
            checkName: 'getConnectionStatusName'
        })
    },
    watch: {
        'connectionParam.name'(newVal) {
            let flag = false;
            let list_mistake = 'Строка пуста';
            const re = /[А-яЁё]/;
            if (newVal) {
                if (newVal.indexOf(' ') == -1) {
                    if (newVal.search(re) == -1) {
                        if (newVal[0].search(/[0-9]/) == -1) {
                            flag = true;
                        } else {
                            list_mistake = 'Название должно начинаться только с символа латинского алфавита';
                        }
                    } else {
                        list_mistake = 'Название должно содержать только символы латинского алфавита';
                    }
                } else {
                    list_mistake = 'Название должно быть без пробелов';
                }
            }
            if (flag) {
                this.correctName.status = true;
                this.correctName.text = '';
            } else {
                this.correctName.status = false;
                this.correctName.text = list_mistake;
            }
            this.checkStatusForm();
        },
        'connectionParam.ip_address'(newVal) {
            let flag = false;
            let list_mistake = 'Строка пуста';
            const re = /[^0-9.]/;
            if (newVal) {
                if (newVal.search(re) == -1) {
                    let namberArray = newVal.split('.')
                    let flagNamber = true
                    namberArray.forEach(el => {
                        if (el.search(re) != -1) {
                            flagNamber = false
                        }
                        if (el == '' ) {
                            flagNamber = false
                        }
                    })
                    if (namberArray.length == 4 && flagNamber) {
                        let flagRangeNumber = true;
                        namberArray.forEach(el => {
                            if (0 > Number(el) || Number(el) > 255) {
                                flagRangeNumber = false;
                            }
                        })
                        if (flagRangeNumber) {
                            flag = true
                        } else {
                            list_mistake = 'В строке число не входит в диапозон 0-255'
                        }
                    } else {
                        list_mistake = 'Строка дожна содержать 4 числа'
                    }
                } else {
                    list_mistake = 'Строка дожна быть следующего вида "X.X.X.X", где X - это число от 0 до 255'
                }
            }
            if (flag) {
                this.correctIp.status = true;
                this.correctIp.text = '';
            } else {
                this.correctIp.status = false;
                this.correctIp.text = list_mistake;
            }
            this.checkStatusForm();
        },
        'connectionParam.port'(newVal) {
            let flag = false;
            let list_mistake = 'Строка пуста';
            if (newVal || newVal==0) {
                if (newVal>0) {
                    flag = true;
                } else {
                    list_mistake = 'Поле должно содержать число больше 0'
                }
            }
            if (flag) {
                this.correctPort.status = true;
                this.correctPort.text = '';
            } else {
                this.correctPort.status = false;
                this.correctPort.text = list_mistake;
            }
            this.checkStatusForm();
        },
        'connectionParam.slot'(newVal) {
            let flag = false;
            let list_mistake = 'Строка пуста';
            if (newVal || newVal==0) {
                if (newVal>=0) {
                    flag = true;
                } else {
                    list_mistake = 'Поле должно содержать число большее либо равное 0'
                }
            }
            if (flag) {
                this.correctSlot.status = true;
                this.correctSlot.text = '';
            } else {
                this.correctSlot.status = false;
                this.correctSlot.text = list_mistake;
            }
            this.checkStatusForm();
        },
        'connectionParam.rack'(newVal) {
            let flag = false;
            let list_mistake = 'Строка пуста';
            if (newVal || newVal==0) {
                if (newVal>0 || newVal==0) {
                    flag = true;
                } else {
                    list_mistake = 'Поле должно содержать число большее либо равное 0'
                }
            }
            if (flag) {
                this.correctRack.status = true;
                this.correctRack.text = '';
            } else {
                this.correctRack.status = false;
                this.correctRack.text = list_mistake;
            }
            this.checkStatusForm();
        },
        'connectionParam.driver'() {
            this.checkStatusForm();
        }
    },
    data() {
        return {
            allFormCorrect: false,
            correctName: {
                status: false,
                text: 'Строка пуста',
                hoverSign: false,
            },
            correctIp: {
                status: false,
                text: 'Строка пуста',
                hoverSign: false,
            },
            correctPort: {
                status: false,
                text: 'Строка пуста',
                hoverSign: false,
            },
            correctSlot: {
                status: false,
                text: 'Строка пуста',
                hoverSign: false,
            },
            correctRack: {
                status: false,
                text: 'Строка пуста',
                hoverSign: false,
            },
            connectionParam: {
                name: '',
                ip_address: '',
                port: null,
                driver: '',
                slot: null,
                rack: null
            },
            statusChange: false,
            drivers: [
                {
                    key: 1,
                    label: 'Snap7',
                    value: 'Snap7'
                },
                {
                    key: 2,
                    label: 'ModbusTCP',
                    value: 'ModbusTCP'
                }
            ]
        }
    },
    methods: {
        ... mapActions({
            addConnection: 'addConnection',
            changeConnection: 'changeConnection',
            checkConnectionName: 'checkConnectionName'
        }),
        ...mapMutations({
            CloseWindow: 'setWindowCreateConnection',
            setStatusName: 'setNameConnectionStaus'
        }),
        checkStatusForm() {
            if ((this.connectionParam.driver == 'Snap7' && this.correctName.status && this.correctIp.status && this.correctPort.status &&
                this.correctSlot.status &&this.correctRack.status) || 
                (this.connectionParam.driver == 'ModbusTCP' && this.correctName.status && this.correctIp.status && this.correctPort.status)) {
                this.allFormCorrect = true;
            } else {
                this.allFormCorrect = false;
            }
        },
        async createConnection() {
            if (this.allFormCorrect) {
                if (this.statusChange) {
                    let item = {
                        param: this.connectionParam,
                        id: this.ParamConnectiob.id
                    }
                    await this.checkConnectionName(item);

                    if (this.checkName) {
                        this.changeConnection(item)
                    } else {
                        ElMessage({
                            dangerouslyUseHTMLString: true,
                            type: 'error',
                            message: 'This name already exists',
                        })
                    }
                } else {
                    let item = {
                        param: this.connectionParam,
                        id: null
                    }
                    await this.checkConnectionName(item);
                    if (this.checkName) {
                        this.addConnection(this.connectionParam)
                    } else {
                        ElMessage({
                            dangerouslyUseHTMLString: true,
                            type: 'error',
                            message: 'This name already exists',
                        })
                    }
                }
            }
        }
    },
    // created() {
    //     this.setStatusName(false);
    // },
    mounted() {
        this.statusChange = false;
        this.setStatusName(false);
        if (this.ParamConnectiob) {
            this.connectionParam.name = this.ParamConnectiob.name
            this.connectionParam.ip_address = this.ParamConnectiob.ip_address
            this.connectionParam.port = this.ParamConnectiob.port
            this.connectionParam.driver = this.ParamConnectiob.driver
            this.connectionParam.slot = this.ParamConnectiob.slot
            this.connectionParam.rack = this.ParamConnectiob.rack
            this.statusChange = true
        }
    },
}
</script>

