<template>
<div class="select-place" :style="{'background': colorbac}" @click="showOption=!showOption">
    <div class="choosing-show-place" v-if="!modelValue">Select type</div>
    <div class="choosing-show-place" v-if="modelValue">{{modelValue.label}}</div>
    <div class="icon-diraction">
        <img src="@/assets/img/strelka-down.svg" :class="{'up': showOption}" draggable="false" alt="">
    </div>
    <Transition name="fade">
    <div class="options-place" style="transition: opacity .4s;" v-if="showOption">
        <li v-for="item in listOption" @click="getOption(item)" :key="item.key">{{item.label}}</li>
    </div>
    </Transition>
</div>
</template>

<script>
export default {
    props: ['listOption', 'modelValue', 'colorbac'],
    emits: ['update:modelValue'],
    data() {
        return {
            showOption: false,
        }

    },
    methods: {
        getOption(element) {
            this.$emit('update:modelValue', element)
        }
    }
}
</script>

