<script>
import SelectElement from './SelectElement.vue';
import { mapGetters, mapActions, mapMutations } from 'vuex';
import { ElMessage } from 'element-plus'

export default {
    components: {
        SelectElement,
    },
    props: ['area_id', 'changeMode', 'variableParam', 'driver'],
    emits: ['closeForm'],
    watch: {
        'paramVariable.name'(newVal) {
            let flag = false;
            let list_mistake = 'Строка пуста';
            const re = /[А-яЁё]/;
            if (newVal) {
                if (newVal.indexOf(' ') == -1) {
                    if (newVal.search(re) == -1) {
                        if (newVal[0].search(/[0-9]/) == -1) {
                            flag = true;
                        } else {
                            list_mistake = 'Название должно начинаться только с символа латинского алфавита';
                        }
                    } else {
                        list_mistake = 'Название должно содержать только символы латинского алфавита';
                    }
                } else {
                    list_mistake = 'Название должно быть без пробелов';
                }
            }
            if (flag) {
                this.correctName.status = true;
                this.correctName.text = '';
            } else {
                this.correctName.status = false;
                this.correctName.text = list_mistake;
            }
            this.checkStatusForm();
        },
        'paramVariable.start'(newVal) {
            let flag = false;
            let list_mistake = 'Строка пуста';
            if (newVal || newVal==0) {
                if (newVal>=0) {
                    flag = true;
                } else {
                    list_mistake = 'Поле должно содержать число большее либо равное 0'
                }
            }
            if (flag) {
                this.correctStart.status = true;
                this.correctStart.text = '';
            } else {
                this.correctStart.status = false;
                this.correctStart.text = list_mistake;
            }
            this.checkStatusForm();
        },
        'paramVariable.time_min'(newVal) {
            let flag = false;
            let list_mistake = 'Строка пуста';
            if (newVal || newVal==0) {
                if (newVal>=0) {
                    flag = true;
                } else {
                    list_mistake = 'Поле должно содержать число большее либо равное 0'
                }
            }
            if (flag) {
                this.correctTimeMin.status = true;
                this.correctTimeMin.text = '';
            } else {
                this.correctTimeMin.status = false;
                this.correctTimeMin.text = list_mistake;
            }
            this.checkStatusForm();
        },
        'paramVariable.bit'(newVal) {
            let flag = false;
            let list_mistake = 'Строка пуста';
            if (newVal || newVal==0) {
                if (newVal>=0) {
                    flag = true;
                } else {
                    list_mistake = 'Поле должно содержать число большее либо равное 0'
                }
            }
            if (flag) {
                this.correctBit.status = true;
                this.correctBit.text = '';
            } else {
                this.correctBit.status = false;
                this.correctBit.text = list_mistake;
            }
            this.checkStatusForm();
        },
        'paramVariable.divide_namber'(newVal) {
            let flag = false;
            let list_mistake = 'Строка пуста';
            if (newVal || newVal==0) {
                if (newVal>0) {
                    flag = true;
                } else {
                    list_mistake = 'Поле должно содержать число больше 0'
                }
            }
            if (flag) {
                this.correctDivideNamber.status = true;
                this.correctDivideNamber.text = '';
            } else {
                this.correctDivideNamber.status = false;
                this.correctDivideNamber.text = list_mistake;
            }
            this.checkStatusForm();
        },
        'paramVariable.type_value_for_write'() {
            this.checkStatusForm();
        },
        'paramVariable.type_value'() {
            this.checkStatusForm();
        },
        'paramVariable.divide'() {
            this.checkStatusForm();
        },
    },
    data() {
        return {
            allFormCorrect: false,
            correctName: {
                status: false,
                text: 'Строка пуста',
                hoverSign: false,
            },
            correctStart: {
                status: false,
                text: 'Строка пуста',
                hoverSign: false,
            },
            correctTimeMin: {
                status: false,
                text: 'Строка пуста',
                hoverSign: false,
            },
            correctBit: {
                status: false,
                text: 'Строка пуста',
                hoverSign: false,
            },
            correctDivideNamber: {
                status: false,
                text: 'Строка пуста',
                hoverSign: false,
            },
            paramVariable: {},
            kindOfType: [
                {
                    key: 1,
                    label: 'int',
                    value: 'int'
                },
                {
                    key: 2,
                    label: 'float',
                    value: 'float'
                },
                {
                    key: 3,
                    label: 'double',
                    value: 'double'
                },
                {
                    key: 4,
                    label: 'bool',
                    value: 'bool'
                }
            ]
        }
    },
    computed: {
        ...mapGetters({
            checkName: 'getVariableStatusName',
        })
    },
    methods: {
        ...mapActions({
            addNewVareable: 'addNewVareable',
            changeVareable: 'changeVareable',
            checkVariableName: 'checkVariableName'
        }),
        ...mapMutations({
            setNameVariableStaus: 'setNameVariableStaus'
        }),
        checkStatusForm() {
            if ((this.correctName.status && this.correctStart.status && this.correctTimeMin.status &&
                    !(this.paramVariable.type_value == this.kindOfType[3]) && !this.paramVariable.divide) ||
                (this.correctName.status && this.correctStart.status && this.correctTimeMin.status && this.correctBit.status &&
                    !this.paramVariable.divide && this.paramVariable.type_value == this.kindOfType[3]) ||
                (this.correctName.status && this.correctStart.status && this.correctTimeMin.status &&
                    !(this.paramVariable.type_value == this.kindOfType[3]) && this.paramVariable.divide &&
                    this.correctDivideNamber.status) ||
                (this.correctName.status && this.correctStart.status && this.correctTimeMin.status &&
                    this.paramVariable.divide && this.correctDivideNamber.status &&
                    this.paramVariable.type_value == this.kindOfType[3] && this.correctBit.status)) {
                        if (this.paramVariable.type_value_for_write != null && this.paramVariable.type_value != null){
                            this.allFormCorrect = true;
                        } else {
                            this.allFormCorrect = false;
                        }
            } else {
                this.allFormCorrect = false;
            }
        },
        async saveVariable() {
            if (this.allFormCorrect) {
                let paramVariable = {
                    name: this.paramVariable.name,
                    start: this.paramVariable.start,
                    type_value_get_data: this.paramVariable.type_value.value,
                    type_value_write_data: this.paramVariable.type_value_for_write.value,
                    if_change: this.paramVariable.if_change,
                    divide: this.paramVariable.divide,
                    divide_number: this.paramVariable.divide_namber,
                    time_write_if_change: this.paramVariable.time_min,
                    bit: this.paramVariable.bit,
                    byte_swap: this.paramVariable.byte_swap,
                    big_endian: this.paramVariable.big_endian,
                    signed: this.paramVariable.signed,
                    area: 'http://127.0.0.1:8000/area/'+ this.area_id +'/'
                }

                if (!this.changeMode) {
                    let item = {
                        param: this.paramVariable,
                        id: null,
                        area_id: this.area_id
                    }
                    await this.checkVariableName(item);
                    if (this.checkName) {
                        this.addNewVareable(paramVariable)
                    } else {
                        ElMessage({
                            dangerouslyUseHTMLString: true,
                            type: 'error',
                            message: 'This name already exists',
                        })
                    }
                } else {
                    let item = {
                        param: this.paramVariable,
                        id: this.variableParam.id,
                        area_id: this.area_id
                    }
                    await this.checkVariableName(item);
                    if (this.checkName) {
                        this.changeVareable({
                            id: this.variableParam.id,
                            data: paramVariable
                        })
                        this.$emit('closeForm')
                    } else {
                        ElMessage({
                            dangerouslyUseHTMLString: true,
                            type: 'error',
                            message: 'This name already exists',
                        })
                    }
                }
            }
        }
    },
    mounted() {
        this.setNameVariableStaus(false);
        if (this.changeMode) {
            this.paramVariable.name = this.variableParam.name
            this.paramVariable.start = this.variableParam.start
            this.kindOfType.find(el => {
                if (el.value==this.variableParam.type_value_get_data) {
                    this.paramVariable.type_value = el
                }
            })
            this.kindOfType.find(el => {
                if (el.value==this.variableParam.type_value_write_data) {
                    this.paramVariable.type_value_for_write = el
                }
            })
            this.paramVariable.if_change = this.variableParam.if_change
            this.paramVariable.divide = this.variableParam.divide
            this.paramVariable.divide_namber = this.variableParam.divide_number
            this.paramVariable.time_min = this.variableParam.time_write_if_change
            this.paramVariable.bit = this.variableParam.bit
            this.paramVariable.byte_swap = this.variableParam.byte_swap
            this.paramVariable.big_endian = this.variableParam.big_endian
            this.paramVariable.signed = this.variableParam.signed

        }
    }
}
</script>

