<template>
    <div class="area-line">
        <div class="header-area">
            <p>{{ area_param.name }}</p>
            <div class="button-place">
                <div class="icon" @click="deleteAndReloadArea">
                    <img class="dustbin" src="@/assets/img/dustbin.svg" alt="Alt">
                </div>
                <div class="icon" @click="showForm = !showForm">
                    <img class="pencil" src="@/assets/img/pencil.svg" alt="Alt">
                </div>
                <div class="add-veriable-button" @click="openWindowCreateVariable">
                    <p class="icon-button">+</p>
                    <p class="name-button">Add new variable</p>
                </div>
            </div>
        </div>
        <div class="form-block" :class="{ 'form-hide':!showForm, 'form-open':showForm && !bigForm, 'form-open-big':showForm && bigForm }">
            <Transition name="fade">
                <FormArea v-if="showForm"
                    :mode_change="true"
                    :index_area="area_index"
                    :connection_id="ConnectionList[indexConnection].id"
                    :driver="ConnectionList[indexConnection].driver"
                    @closeEnd="showForm = !showForm"
                    @onBigType="bigForm=$event"
                />
            </Transition>
        </div>
        <div class="vl-area" />
        <VariableList :variableList="area_param.value" :area_id="area_param.id" :driver="this.ConnectionList[this.indexConnection].driver" />
    </div>
</template>

<script>
import FormArea from './FormArea.vue'
import VariableList from './VariableList.vue'
import { mapActions, mapMutations, mapGetters } from 'vuex'

export default {
    data() {
        return {
            showForm: false,
            bigForm: false
        }
    },
    components: {
        FormArea,
        VariableList
    },
    props: [
            'area_param', 'area_index', 'indexConnection'
    // 'name', 'id', 'connecton_id', 'indexArea'
    ],
    computed: {
        ...mapGetters({
            ConnectionList: 'getConnectionList',
        })
    },
    methods: {
        ...mapActions({
            deleteArea: 'deleteArea'
        }),
        ...mapMutations({
            setWindowVariable: 'setWindowVariableAdd',
            setWindowVariableName: 'setWindowVariableAddName',
            setWindowVariableArea: 'setWindowVariableAddArea',
            setDriverForWindowVariableAdd: 'setDriverForWindowVariableAdd',
        }),
        openWindowCreateVariable() {
            this.setWindowVariable(true)
            this.setWindowVariableName('Add new variable for '+this.area_param.name+" of areas")
            this.setWindowVariableArea(this.area_param)
            this.setDriverForWindowVariableAdd(this.ConnectionList[this.indexConnection].driver)
        },
        async deleteAndReloadArea() {
            await this.deleteArea(this.area_param.id)
        },
    }
}
</script>

