<template>
  <div class="login-background">
    <div class="window-login">
    <!-- <div class="window-login" v-if="true"> -->
      <div class="name-window">
        <label class="text">Sign in GetData</label>
      </div>
      <div class="form-input">
        <div>Username</div>
        <input type="text" v-model="Username" :disabled="procReques" @keyup.enter="clickSignIn">
      </div>
      <div class="form-input">
        <div>Password</div>
        <input type="password" v-model="Password" :disabled="procReques" @keyup.enter="clickSignIn">
      </div>
      <div class="button-login" @click="clickSignIn">
        <label class="text" v-show="!procReques">Sign in</label>
        <LoaderIcon v-show="procReques" />
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import LoaderIcon from './LoaderIcon.vue';

export default {
  name: 'LoginWindow',
  components: {
    LoaderIcon
  },
  data() {
    return {
      Username: '',
      Password: '',
    }
  },
  computed: {
    ...mapGetters({
      procReques: 'getProcessRequest'
    })
  },
  methods: {
    ... mapActions({
      logIn: 'postLogin'
    }),
    async clickSignIn() {
      let data = {
        'username': this.Username,
        'password': this.Password
      }
      this.logIn(data)
    }
  }
}
</script>

