<template>
    <div class="windowVariable">
        <div class="header-form">
            <div class="button-place">
                <button @click="deleteVareable({
                    id: Variable.vareable.id,
                    area_id: Variable.area_id
                })">
                    <img src="@/assets/img/dustbin.svg" alt="">
                </button>
                <button @click="formShow=!formShow">
                    <img src="@/assets/img/pencil.svg" alt="">
                </button>
            </div>
            <h2>{{ Variable.vareable.name }}</h2>
            <div class="button-close" @click="setStatusWindowVariableParamStatus(false)">×</div>
        </div>
        <FormVariable
            :area_id="Variable.area_id"
            :variableParam="Variable.vareable"
            :changeMode="true"
            :driver="Variable.driver"
            @closeForm="formShow = false"
            v-if="formShow"
        />
        <div class="vl-variable" />
        <button 
            class="update-button"
            :class="{
                'off-status-button': !autoAbdateButton,
                'on-status-button': autoAbdateButton
            }"
            @click="clickButton()"
        >Auto Update</button>
        <highcharts :options="chartOptions"></highcharts>
    </div>
</template>

<script>
    import FormVariable from './simpleElement/FormVariable.vue';
    import { mapMutations, mapGetters, mapActions } from 'vuex';
    import {Chart} from 'highcharts-vue';
    import axios from '../../config-axios';

    export default {
        data() {
            return {
                autoAbdateButton: false,
                formShow: false,
                lastPointTime: null,
                flagStopTimeOut: true,
                chartOptions: {
                    credits:{
                        enabled: false
                    },
                    chart: {
                        backgroundColor: null
                    },
                    title: {
                        text: 'asdasdas',
                        style: {
                            "display": "none",
                            "color": "#F1F3FF",
                            "fontSize": "1.125em"
                        }
                    },
                    yAxis: {
                        name: '',
                        lineColor: '#F1F3FF',
                        lineWidth: 1,
                        tickColor: '#F1F3FF',
                        tickLength: 5,
                        tickWidth: 1,
                        labels: {
                            style: {
                                color: '#F1F3FF'
                            }
                        },
                        gridLineWidth: 0,
                        title: {
                            text: ''
                        }
                    },
                    xAxis: {
                        name: 'time',
                        type: 'datetime',
                        lineColor: '#F1F3FF',
                        lineWidth: 1,
                        tickColor: '#F1F3FF',
                        tickLength: 5,
                        tickWidth: 1,
                        labels: {
                            style: {
                                color: '#F1F3FF'
                            }
                        },
                        gridLineWidth: 0,
                        title: {
                            text: 'time',
                            style:{
                                color: '#F1F3FF',
                                fontSize:'0.8em'
                            }
                        }
                    },
                    time: {
                        timezone: 'Europe/Minsk',
                        timezoneOffset: -3 * 60,
                    },
                    legend: {
                        enabled: false
                    },
                    series: [{
                        data: [] // sample data
                    }]
                }
            }
        },
        watch: {
            test_but(new_val) {
                if (new_val) {
                    this.flagStopTimeOut = false;
                    setTimeout(this.startTimerPoint, 1000);
                } else {
                    this.flagStopTimeOut = true;
                }
            },
            'Variable.vareable.id'() {
                this.lastPointTime = null;
                this.chartOptions.series[0].data = [];
                this.getData();
            }
        },
        components: {
            FormVariable,
            highcharts: Chart
        },
        computed: {
            ...mapGetters({
                Variable: 'getWindowVariableParam',
            }),
        },
        methods: {
            ...mapMutations({
                setStatusWindowVariableParamStatus :'setStatusWindowVariableParamStatus',
            }),
            ...mapActions({
                deleteVareable: 'deleteVareable'
            }),
            getData() {
                axios.post('connection/get_point/', { id_variable: this.Variable.vareable.id }).then(response => {
                    response.data.forEach(el=>{
                        this.chartOptions.series[0].data.push(el);
                        this.lastPointTime = el[0];
                    });
                })
            },
            clickButton() {
                if (this.autoAbdateButton) {
                    this.autoAbdateButton = false;
                    this.flagStopTimeOut = true;
                } else {
                    this.autoAbdateButton = true;
                    this.flagStopTimeOut = false;
                    this.startTimerPoint();
                }
            },
            startTimerPoint() {
                if (this.lastPointTime != null) {
                    axios.post('connection/get_point/', {
                        id_variable: this.Variable.vareable.id,
                        last_time: this.lastPointTime/1000
                    }).then(response => {
                        response.data.forEach(el=>{
                            if (this.chartOptions.series[0].data.length >= 100) {
                                this.chartOptions.series[0].data.splice(0, 1)
                            }
                            this.chartOptions.series[0].data.push(el)
                        })
                        if (response.data.length != 0) {
                            this.lastPointTime = response.data[response.data.length-1][0];
                        }
                    })
                }
                if (!this.flagStopTimeOut) {
                    setTimeout(this.startTimerPoint, 4000);
                }
            },
        },
        mounted() {
            this.getData();
        },
        unmounted() {
            this.flagStopTimeOut = true;
        }
    }
</script>

